// ** React Import
import React, { useRef, useState } from 'react'

// ** Custom Components
import UILoader from '@components/ui-loader'
import Sidebar from '@components/sidebar'
import InputGroup from '@components/input-group'

// ** Third Party Components
import classnames from 'classnames'
import { Button, Form, FormGroup } from 'reactstrap'

// ** Store & Actions
import { addUserCar, userCarFormChange } from '@store/UserCars/actions'
import { useDispatch, useSelector } from 'react-redux'

const SidebarNewUserCars = ({
  open,
  toggleSidebar
}) => {
  const formEl = useRef(null)
  const [isOpened, setIsOpened] = useState(false)
  const store = useSelector(state => state.userCars),
    dispatch = useDispatch()
  const { userCarFormData, formErrors, isLoading, isSubmitting } = store

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(addUserCar())
  }

  const handleFormChange = (name, value) => {
    dispatch(userCarFormChange(name, value))
  }

  const onOpened = () => {
    setIsOpened(true)
  }

  return (
    <Sidebar
      onOpened={onOpened}
      size='lg'
      open={open}
      title='New UserCar'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit} innerRef={formEl}>
        <InputGroup
          type={'text'}
          label={'Car Make'}
          error={formErrors['carMake']}
          name={'carMake'}
          placeholder={'Car Make'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.carMake}
          required={true}
          className={classnames({ 'is-invalid': formErrors['carMake'] })}
        />
        <InputGroup
          type={'text'}
          label={'Car Model'}
          error={formErrors['carModel']}
          name={'carModel'}
          placeholder={'Car Model'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.carModel}
          required={true}
          className={classnames({ 'is-invalid': formErrors['carModel'] })}
        />
        <InputGroup
          type={'text'}
          label={'Engine Capacity'}
          error={formErrors['engineCapacity']}
          name={'engineCapacity'}
          placeholder={'Engine Capacity'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.engineCapacity}
          required={true}
          className={classnames({ 'is-invalid': formErrors['engineCapacity'] })}
        />
        <InputGroup
          type={'text'}
          label={'Registration Number'}
          error={formErrors['registrationNumber']}
          name={'registrationNumber'}
          placeholder={'Registration Number'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.registrationNumber}
          required={true}
          className={classnames({ 'is-invalid': formErrors['registrationNumber'] })}
        />
        <InputGroup
          type={'number'}
          label={'Year of Manufacture'}
          error={formErrors['yearOfManufacture']}
          name={'yearOfManufacture'}
          placeholder={'Year of Manufacture'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.yearOfManufacture}
          required={true}
          className={classnames({ 'is-invalid': formErrors['yearOfManufacture'] })}
        />
        <InputGroup
          type={'text'}
          label={'Car Status'}
          error={formErrors['carStatus']}
          name={'carStatus'}
          placeholder={'Car Status'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={userCarFormData.carStatus}
          required={true}
          className={classnames({ 'is-invalid': formErrors['carStatus'] })}
        />
        <FormGroup row className='mr-0 ml-0'>
          <UILoader blocking={isLoading} className='mr-1'>
            <Button type='submit' color='primary'>
              Submit
            </Button>
          </UILoader>
          <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
            Cancel
          </Button>
        </FormGroup>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewUserCars
