// ** React Imports
// ** Custom Components
import TableAction from '@components/table/TableAction'

// ** Store & Actions
import { getUserCar } from '@store/UserCars/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Archive, FileText } from 'react-feather'

export const columns = [
  {
    name: 'Make',
    minWidth: '229px',
    selector: 'email',
    sortable: true,
    cell: row => row.carMake
  },
  {
    name: 'Model',
    minWidth: '229px',
    selector: 'phoneNumber',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.carModel}</span>
  },
  {
    name: 'Engine',
    minWidth: '172px',
    selector: 'role',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.engineCapacity}</span>
  },
  {
    name: 'Year',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.yearOfManufacture}</span>
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => {
      const actions = [
        {
          link: `/users/${row.userId}/userCars/view/${row._id}`,
          handler: () => store.dispatch(getUserCar(row._id)),
          label: 'Details',
          icon: (<FileText size={14} className='mr-50' />)
        },
        {
          link: `/users/${row.userId}/userCars/edit/${row._id}`,
          handler: () => store.dispatch(getUserCar(row._id)),
          label: 'Edit',
          icon: (<Archive size={14} className='mr-50' />)
        }
      ]
      return (
        <TableAction actions={actions} />
      )
    }
  }
]
