// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Store & Actions
import { getUser, deleteUser } from '@store/Users/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import { Slack, User, Bell, Award, Settings, Database, Edit2, MoreVertical, FileText, Trash2, Archive } from 'react-feather'

// ** Renders Client Columns
const renderClient = row => {
  const stateNum = Math.floor(Math.random() * 6),
    states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
    color = states[stateNum]

  if (row.profilePictureURL !== undefined && row.profilePictureURL.length) {
    return <Avatar className='mr-1' img={row.profilePictureURL} width='32' height='32' />
  } else {
    return <Avatar color={color || 'primary'} className='mr-1' content={row.userName || 'Carhoot'} initials />
  }
}

// ** Renders Role Columns
const renderMembership = row => {
  const roleObj = {
    free: {
      class: 'text-primary',
      icon: User
    },
    elite: {
      class: 'text-danger',
      icon: Award
    }
  }

  const Icon = roleObj[row.membershipType] ? roleObj[row.membershipType].icon : Bell

  return (
    <span className='text-truncate text-capitalize align-middle'>
      <Icon size={18} className={`${roleObj[row.membershipType] ? roleObj[row.membershipType].class : ''} mr-50`} />
      {row.membershipType}
    </span>
  )
}

const statusObj = {
  pending: 'light-warning',
  active: 'light-success',
  inactive: 'light-secondary'
}

export const columns = [
  {
    name: 'User',
    minWidth: '297px',
    selector: 'fullName',
    sortable: true,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link
            to={`/users/view/${row._id}`}
            className='user-name text-truncate mb-0'
            onClick={() => store.dispatch(getUser(row._id))}
          >
            <span className='font-weight-bold'>{row.firstName} {row.lastName}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Email',
    minWidth: '229px',
    selector: 'email',
    sortable: true,
    cell: row => row.email
  },
  {
    name: 'Phone Number',
    minWidth: '229px',
    selector: 'phoneNumber',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.countryCode}{row.phoneNumber}</span>
  },
  {
    name: 'Membership',
    minWidth: '172px',
    selector: 'membershipType',
    sortable: true,
    cell: row => renderMembership(row)
  },
  {
    name: 'Status',
    minWidth: '138px',
    selector: 'status',
    sortable: true,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status]} pill>
        {row.status}
      </Badge>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <UncontrolledDropdown>
        <DropdownToggle tag='div' className='btn btn-sm'>
          <MoreVertical size={14} className='cursor-pointer' />
        </DropdownToggle>
        <DropdownMenu right>
          <DropdownItem
            tag={Link}
            to={`/users/view/${row._id}`}
            className='w-100'
            onClick={() => store.dispatch(getUser(row._id))}
          >
            <FileText size={14} className='mr-50' />
            <span className='align-middle'>Details</span>
          </DropdownItem>
          <DropdownItem
            tag={Link}
            to={`/users/edit/${row._id}`}
            className='w-100'
            onClick={() => store.dispatch(getUser(row._id))}
          >
            <Archive size={14} className='mr-50' />
            <span className='align-middle'>Edit</span>
          </DropdownItem>
        </DropdownMenu>
      </UncontrolledDropdown>
    )
  }
]
