import { useEffect, useState } from "react"
import { useDispatch, useSelector } from 'react-redux'
import CONSTANTS from '@constants'
// ** Invoice List Sidebar
import Sidebar from './UserCarSidebar'

// ** Columns
import { columns } from './UserCarColumns'

// ** Store & Actions
import { getAllUserCars, getUserCarList } from '@store/UserCars/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const { STATUS } = CONSTANTS

const UserCarsList = () => {
  // ** States
  const [currentStatus, setCurrentStatus] = useState({ value: '', label: 'Select Status' })
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const { allData, data, total } = useSelector(state => state.userCars),
    { selectedUser } = useSelector(state => state.users),
    dispatch = useDispatch()

  // ** Get data on mount
  useEffect(() => {
    dispatch(getUserCarList({
      userId: selectedUser._id
    }))
  }, [selectedUser])

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const getAllUserCarList = () => {
    return (dispatch) => {
      dispatch(getAllUserCars({ userId: selectedUser._id }))
    }
  }

  const statusOptions = [{ value: '', label: 'Select Status' }]
  for (const sKey in STATUS) {
    statusOptions.push({ value: sKey, label: STATUS[sKey] })
  }

  const searchParams = {
    userId: selectedUser._id
  }

  const customFilters = [
    {
      key: 'status',
      options: statusOptions,
      value: currentStatus,
      onChange: setCurrentStatus
    }
  ]


  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={allData}
        data={data}
        total={total}
        columns={columns}
        filters={customFilters}
        searchParams={searchParams}
        getList={getUserCarList}
        getAllList={getAllUserCarList}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New Car'}
        customHeaderLabel={'Search User Cars'}
        tableLabel={'User Cars'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default UserCarsList
