// ** Reactstrap
import { useSelector } from "react-redux"
import { Card, CardHeader, CardBody, Badge, UncontrolledTooltip, Button } from 'reactstrap'

const PlanCard = ({ selectedUser }) => {
  const { membershipType, membershipExpiryDate } = selectedUser
  const { rolesConfig } = useSelector(state => state.roles)
  const { membership: membershipConfig } = rolesConfig.user

  return (
    <Card className='plan-card border-primary'>
      <CardHeader className='d-flex justify-content-between align-items-center pt-75 pb-1'>
        <h5 className='mb-0'>Current Plan</h5>
        <Badge id='plan-type' className='text-capitalize' color='light-primary'>
          {membershipType !== undefined && membershipConfig[membershipType] ? membershipConfig[membershipType] : 'Basic'}
        </Badge>
        <UncontrolledTooltip placement='top' target='plan-type'>
          Plan Type
        </UncontrolledTooltip>
      </CardHeader>
      <CardBody>
        <Badge id='plan-expiry-date' color='light-secondary'>
          {membershipExpiryDate !== undefined && membershipExpiryDate ? membershipExpiryDate  : 'No Expiry Date'}
        </Badge>
        <UncontrolledTooltip placement='top' target='plan-expiry-date'>
          Expiry Date
        </UncontrolledTooltip>
        <Button.Ripple className='text-center my-1' color='primary' block>
          Update Plan
        </Button.Ripple>
      </CardBody>
    </Card>
  )
}

export default PlanCard
