// ** React Imports
import { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Store & Actions
import { getUser } from '@store/Users/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Reactstrap
import { Alert, Col, Row } from 'reactstrap'

// ** User View Components
import PlanCard from './PlanCard'
import UserInfoCard from './UserInfoCard'
import UserPersonalInfoCard from './UserPersonalInfoCard'
import Orders from './Order'
import UserCars from '../../UserCars/List/UserCars'

// ** Custom Components
import UILoader from '@components/ui-loader'
import CarhootBreadCrumbs from '@components/breadcrumbs/CarhootBreadCrumbs'

// ** Styles
import '@styles/react/apps/app-users.scss'

const UserView = () => {
  // ** Vars
  const store = useSelector(state => state.users),
    dispatch = useDispatch(),
    { id } = useParams()
  const { isLoading } = store
  // ** Get user on mount
  useEffect(() => {
    dispatch(getUser(id))
  }, [dispatch])

  return store.selectedUser !== null && store.selectedUser !== undefined ? (
    <UILoader blocking={isLoading}>
      <CarhootBreadCrumbs
        breadCrumbTitle='Breadcrumbs'
        breadCrumbItems={
          [
            {
              link: `/users/list`,
              title: 'Users'
            },
            {
              title: 'User profile'
            }
          ]
        }
      />
      <div className='app-user-view'>
        <Row>
          <Col xl='12'>
            <UserInfoCard selectedUser={store.selectedUser} />
          </Col>
        </Row>
        <Row>
          <Col xl='8' lg='8' md='12'>
            <UserPersonalInfoCard selectedUser={store.selectedUser} />
          </Col>
          <Col xl='4' lg='4' md='12'>
            <PlanCard selectedUser={store.selectedUser} />
          </Col>
        </Row>
        <Row>
          <Col xl='12' lg='12' md='12'>
            <UILoader blocking={isLoading}>
              <div className='app-user-list'>
                <UserCars />
              </div>
            </UILoader>
          </Col>
          <Col xl='12' lg='12' md='12'>
            <UILoader blocking={isLoading}>
              <div className='app-user-list'>
                <Orders />
              </div>
            </UILoader>
          </Col>
          <Col xl='12' lg='12' md='12'>
            <UILoader blocking={isLoading}>
              <div className='app-user-list'>
                <Orders />
              </div>
            </UILoader>
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>User not found</h4>
      <div className='alert-body'>
        User with id: {id} doesn't exist. Check list of all Users: <Link to='/users/list'>Users List</Link>
      </div>
    </Alert>
  )
}

export default UserView
